$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-gutter-right: 24px;
  --mt-gutter-left: 24px;

  display: flex;
  flex-wrap: wrap;
  margin-right: calc(-0.5 * var(--mt-gutter-right));
  margin-left: calc(-0.5 * var(--mt-gutter-left));

  @each $mt-breakpoint-key, $mt-breakpoint-value in $mt-breakpoints {
    @media (min-width: $mt-breakpoint-value) {
      margin-right: calc(-0.5 * var(--mt-gutter-#{$mt-breakpoint-key}-right));
      margin-left: calc(-0.5 * var(--mt-gutter-#{$mt-breakpoint-key}-left));
    }
  }
}
